* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.navbar {
    display: flex;
    justify-content: space-around;
    align-items: center;
    height: 75px;
    background-color: aquamarine;
    color: white;
}

.logo {
    font-size: 30px;
}

.nav-links {
    display: flex;
    justify-content: center;
    list-style: none;
    width: 75%;
}

.button {
    background-color: cadetblue !important;
    border-radius: 5px;
}